@import '../sections/variables.less';

.toolBar {
  position: fixed;
  bottom: 0;
  z-index: 99;
  width: 100%;
  background: #ffffff;

  button {
    height: 34px;
    background: rgba(19, 70, 255, 0.08);
    border-radius: 3px;
    margin-left: 15px;
    &:hover,
    &:active,
    &:focus {
      background: rgba(19, 70, 255, 0.08);
    }
  }
  i {
    padding: 15px 16px;
    color: #1346ff;
    font-size: 34px;
  }
}

.root {
  color: #fff;
  padding: 100px 0;
  background-color: #0e1421;

  .content {
    max-width: @maxWidth;
    margin: 0 auto;
  }

  .left {
    h2 {
      color: #fff;
      font-size: 32px;
      opacity: 0.8;
    }
    p {
      color: #a8b0bb;
      font-size: 20px;
    }
  }

  .right {
    line-height: 38px;

    text-align: right;

    & > div {
      display: inline-block;
      text-align: left;
    }
    a {
      color: #fff;
      display: inline-block;
    }
    .row {
      margin-bottom: 20px;
    }
    .category {
      font-size: 16px;
      opacity: 0.8;
      display: inline-block;
      width: 8em;
    }
    .details {
      display: inline-block;
    }
    .icon {
      font-size: 26px;
      margin-right: 1em;
    }
    .link {
      display: inline-block;
      width: 10em;
      opacity: 0.4;
      font-size: 14px;
    }
  }
}

.logo {
  // width: 120px;
  height: 40px;
  margin-bottom:20px;
}

