.maxWithdraw {
  font-size: 14px;
  font-weight: 500;
  color: #333333;
  line-height: 17px;
  display: inline-block;
  margin-left: 20px;
  text-align: left;
  &.mobile{
    // margin-bottom: 8px;
    margin-left: 0;
  }
  span {
    font-weight: bold;
  }
}

.actionBtns {
  margin-top: 36px;
  button {
    width: 100%;
  }
}

.commonModal {
  .invitorInfo {
    font-size: 16px;
    font-weight: bold;
    color: #333333;
  }
  :global {
    .ant-modal-body {
      padding: 36px 60px;
    }
    .ant-descriptions-item > span {
      color: #333;
    }
    .ant-descriptions-item > span:nth-child(1) {
      font-weight: bold;
    }
  }
  h4 {
    font-size: 24px;
    font-weight: bold;
    color: #333333;
    line-height: 30px;
    text-align: center;
    margin-bottom: 40px;
    line-height: 1;
  }
  
}
.repay {
  margin-top: 16px;
  &.mobile{
    margin-top: 0;
    margin-bottom: 8px;
  }
  p {
    font-size: 14px;
    font-family: Gilroy-Medium, Gilroy;
    font-weight: 500;
    color: #333333;
    line-height: 17px;
    margin: 20px 0 36px;
  }
}

