@import '../variables.less';
@import '../../common.less';

:global(.mobile) {
  .root {
    background-color: #f9f9f9;
    padding: 30px 15px 100px;
    h3 {
      color: #333; // changed by 蒜苗 2021-03-31
      font-size: 20px;
      opacity: 0.8;
      line-height: 1;
    }

    h3 + p {
      margin-bottom: 0px;
      margin-top: 16px;
      font-size: 13px;
    }

    i {
      font-size: 28px;
      margin-top: 50px;
    }

    .icon {
      width: 28px;
      margin-top: 50px;
    }
  }
}
.root {
  text-align: center;
  color: #fff;
  background-color: #f9f9f9;
  padding-bottom: 100px;
  padding-top: 100px;

  i {
    color: #a8b0bb;
    font-size: 55px;
    margin-top: 70px;
  }

  .icon {
    width: 55px;
    margin-top: 70px;
  }
}

