@import '../variables.less';
@import '../../common.less';

:global(.mobile) {
  .root {
    padding-bottom: 80px;
    margin: 0;
    background-color: #fff;
    .message {
      padding: 0;
    }
    & > div {
      padding: 40px 24px 0;
      border-radius: 4px;
    }
    h3 {
      font-size: 18px;
      color: #333;
      padding: 0;
      margin: 0;
    }
    .advantage {
      font-size: 26px;
      margin: 40px auto 30px;
      font-weight: bold;
    }
    .desc {
      font-size: 14px;
      margin-bottom: 30px;
    }
    .imgContainer {
      img {
        margin-top: 0;
        width: 100%;
        margin-bottom: 0;
      }
    }
  }
}

.root {
  background-color: #fff;
  margin-bottom: 60px;

  & > div {
    background-color: #fff;
  }

  .message {
    padding: 0 120px;
  }
  .imgContainer {
    text-align: center;
    img {
      margin-top: 100px;
      margin-bottom: 50px;
    }
  }
  h3 {
    color: #383838;
    font-size: 20px;
    line-height: 20px;
    padding-top: 120px;
  }
  p.advantage {
    font-size: 36px;
    line-height: 42px;
    color: #383838;
    margin: 5% 0 5%;
    font-weight: bold;
  }

  p.desc {
    color: #808080;
    font-size: 16px;
    line-height: 22px;
  }
}

