.homeHeader {
  background-color: transparent;
  color: #fff;
  font-size: 24px;
  line-height: 60px;
  padding: 2px 16px;
  position: absolute;
  top: 0;
  z-index: 99;
  width: 100%;
}

.root {
  // position: absolute;
  // left: 0;
  // top: 0;
  // right: 0;
  z-index: 9;
  background-color: #1346ff;
  border-bottom: 1px solid #1346ff;
  &.light {
    background-color: #f9fafc;
    border-bottom: 1px solid #eeeeee;
    .connectBtn {
      background-color: #1346ff;
    }
    &.mobileLight {
      :global {
        .ant-menu-item,
        .ant-menu-submenu-title {
          padding: 0 15px;
        }
      }
    }
    :global {
      .ant-menu {
        background-color: #f9fafc;
        color: #333333;
        border-bottom-color: #eeeeee;

        .ant-menu-item:hover {
          border-bottom-color: transparent;
        }
        .ant-menu-item.ant-menu-item-selected {
          border-bottom-color: #1346ff;
        }

        .ant-menu-submenu-title:hover,
        &:not(.ant-menu-inline) .ant-menu-submenu-open,
        .ant-menu-submenu-active,
        &.ant-menu-horizontal > .ant-menu-submenu-selected,
        &.ant-menu-horizontal > .ant-menu-submenu:hover {
          color: #333;
        }
        &.ant-menu-horizontal > .ant-menu-item > a {
          color: #333333;
        }
      }
    }
  }

  .connectWpr {
    text-align: right;
    padding-right: 12px;
  }

  .rightContent {
    display: flex;
    align-items: center;
    justify-content: flex-end;
  }
  .connectBtn {
    height: 34px;
    font-size: 14px;
    margin-right: 40px;
    background: rgba(229, 238, 255, 0.14);
    color: #fff;
    border: 1px solid #3b6cff;
    border-radius: 100px;
  }

  .network {
    border-radius: 100px;
    border: 1px solid #bbc0c5;
    font-size: 12px;
    color: #6a737d;
    text-align: center;
    padding: 8px 20px;
    margin-right: 20px;
    min-width: 86px;

    .icon {
      display: inline-block;
      height: 8px;
      width: 8px;
      border-radius: 100%;
      background: #1346ff;
      position: relative;
      right: 6px;
    }
  }

  :global {
    .ant-menu-horizontal {
      border-bottom: 0;
      line-height: 60px;
    }
    .ant-menu-item {
      &:hover,
      &-active,
      &-open,
      &-selected {
        border-bottom-color: #1346ff;
      }
    }
  }
}

.dderivatives {
  font-size: 24px;
  padding-left: 42px;
}

.accountInfo {
  span {
    display: inline-block;
    height: 32px;
    line-height: 32px;
    border: 1px solid #1346ff;
    border-right: none;
    background: #fff;
    color: #1346ff;
    font-size: 14px;
    padding: 0px 10px;
  }

  span:first-child {
    border-radius: 5px 0 0 5px;
    background: #1346ff;
    color: #fff;
  }

  span:last-child {
    border-radius: 0 5px 5px 0;
    border-right: 1px solid #1346ff;
    cursor: pointer;
    background: #f2f5ff;
  }
}

